body.desarrollo .ed-container {
  padding-top: 1.25rem;
  margin-bottom: 10px;
  outline: 1px solid tomato;
  position: relative;
}

body.dev .ed-container .ed-container,
body.desarrollo .ed-container .ed-container {
  margin-bottom: 0;
}

body.dev .ed-container:before,
body.desarrollo .ed-container:before {
