:root {
  --first-color: #007BDF;
  --first-color-alt: #006DC6;
  --second-color: #00CBFF;
  --second-color-alt: #00BFF0;
  --third-color: #FF3C32;
  --third-color-alt: #FF2419;
  --accent-color: #FBA905;
  --accent-color-alt: #F1A000;
  --light-color: #EAEAEA;
  --dark-color: #282D31;
  --dark-color-alt: #141618;
  --border-color: #DFE0E0;
  --text-color: #53575A;
  --body-bg: #FCFCFC;
  /* Usar nombres de color que comiencen con --color
     se conservan los anteriores nombres por compatibilidad
  */
  --color-first: #007BDF;
  --color-first-alt: #006DC6;
  --color-second: #00CBFF;
  --color-second-alt: #00BFF0;
  --color-third: #FF3C32;
  --color-third-alt: #FF2419;
  --color-accent: #FBA905;
  --color-accent-alt: #F1A000;
  --color-light: #F0F0F0;
  --color-dark: #282D31;
  --color-dark-alt: #141618;
  --color-border: #DFE0E0;
  --color-text: #53575A;
  --color-text-light: #BABDBF;
  --color-body-bg: #FCFCFC;
}

.color {
  color: var(--color);
}

.background {
  background-color: var(--color);
}

.first-color, .color-first {
  --color: var(--first-color);
  --color-alt: var(--first-color-alt);
}

.second-color, .color-second {
  --color: var(--second-color);
  --color-alt: var(--second-color-alt);
}

.third-color, .color-third {
  --color: var(--third-color);
  --color-alt: var(--third-color-alt);
}

.accent-color, .color-accent {
  --color: var(--accent-color);
  --color-alt: var(--accent-color-alt);
}

.dark-color, .color-dark {
  --color: var(--dark-color);
  --color-alt: var(--dark-color-alt);
}

.text-color, .color-text {
  --color: var(--text-color);
  --color-alt: var(--dark-color);
}

.light-color, .color-light {
  --color: var(--light-color);
  --color-alt: var(--border-color);
}

.first-color-alt {
  --color: var(--first-color-alt);
}

.second-color-alt {
  --color: var(--second-color-alt);
}

.third-color-alt {
  --color: var(--third-color-alt);
}

.accent-color-alt {
  --color: var(--accent-color-alt);
}

.dark-color-alt {
  --color: var(--dark-color-alt);
}

.border-color {
  --color: var(--border-color);
}

.text-color {
  --color: var(--text-color);
}

.body-bg {
  --color: var(--body-bg);
}

.body-bg.color-card [class*=ratio] {
  border: 1px solid var(--border-color);
  border-bottom: none;
}

.main-sidebar {
  border-right: 1px solid var(--border-color);
}

*,
*::before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

@media screen and (min-width: 1024px) {
  .main-header {
    height: var(--header-height);
  }
}

@media screen and (min-width: 1024px) {
  :root {
    --header-height: 4rem;
  }
}

.main-header + .l-section,
.main-header + [class*=topbar] {
  margin-top: var(--header-height) !important;
}

@media screen and (max-width: 1023px) {
  .main-footer {
    text-align: center;
  }
  .main-footer span {
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 1023px) {
  .l-section {
    margin-bottom: 2rem;
  }
}

.user {
  display: flex;
  align-items: center;
}

.user__avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  flex: none;
}

.user__data {
  margin-left: 1rem;
}

.user__name {
  font-weight: 700;
  font-family: "Open Sans", "sans-serif";
}

.user__country {
  display: flex;
  align-items: center;
}

.user__flag {
  width: 1.5rem;
  height: 1rem;
  margin-right: .5rem;
}

.home-users .user {
  position: relative;
}

@media screen and (min-width: 1024px) {
  .home-users .user {
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 1023px) {
  .home-users .user {
    width: 80%;
    margin: 0 auto 1rem;
  }
}

@media screen and (min-width: 1024px) {
  .home-users .user + .user::after {
    content: "";
    display: block;
    width: 1px;
    background: #FF3C32;
    height: 1.6rem;
    bottom: calc(100px + 0.2rem);
    left: 50px;
    position: absolute;
  }
}

.user--profile {
  align-items: flex-start;
}

.user--profile p {
  font-size: var(--small-font-size);
  margin-top: .5rem !important;
  line-height: 1.6;
}

.user--profile .user__country {
  margin-top: .25rem;
}

.user--profile .user__flag {
  margin-top: 0 !important;
  margin-right: .25rem !important;
}

.talk .user {
  margin-bottom: 0;
}

@media screen and (max-width: 1023px) {
  .talk .user {
    display: block;
    text-align: center;
  }
  .talk .user .user__avatar {
    width: 60px;
    height: 60px;
    display: block;
  }
  .talk .user .user__data {
    margin-left: 0;
    line-height: 1.3;
  }
  .talk .user .user__country {
    display: flex;
    justify-content: center;
    font-size: 0;
  }
  .talk .user .user__country img {
    margin: .5rem 0 0;
  }
  .talk .user .user__avatar {
    margin: auto;
  }
}

.card--subscription {
  height: auto !important;
}

.card--subscription .card__title {
  font-family: Lato, "sans-serif";
}

.card--subscription .card__img {
  width: 138px;
  flex: none;
}

@media screen and (max-width: 1023px) {
  .card--subscription .card__img {
    display: none;
  }
}

.card--subscription .card__img img {
  display: block;
}

.card--subscription .card__content {
  align-items: flex-start;
}

@media screen and (max-width: 1023px) {
  .card--subscription .card__content {
    display: block;
    text-align: center;
  }
}

.card--subscription [class*=button] {
  display: inline-flex;
  font-size: var(--h3-font-size);
  padding: .4rem 1rem;
  align-items: center;
}

.card--subscription [class*=button] del {
  margin: 0 .25rem;
}

.card--subscription [class*=button]::before {
  content: "";
  display: inline-block;
  width: 2rem;
  height: 1.5rem;
  background: url(../assets/img/ticket.svg) no-repeat center/contain;
  margin-right: .25rem;
}

.card--subscription + span {
  padding-top: .5rem;
  display: block;
}

@media screen and (max-width: 1023px) {
  .card--subscription + span {
    text-align: center;
  }
}

.card--tde [class*=button] {
  font-size: 0;
}

.card--tde [class*=button] span {
  font-size: 1.25rem;
}

.edcamp-features {
  margin: 0;
  padding: 0;
  list-style: none;
}

.edcamp-features li::before {
  content: "";
  display: inline-block;
  margin-right: .4rem;
  width: 1rem;
  height: 1rem;
  background: url(../img/check.svg) no-repeat center;
}

@media screen and (max-width: 1023px) {
  .edcamp-features li {
    font-size: var(--smaller-font-size);
  }
}

.social-icons {
  display: flex;
  justify-content: flex-end;
}

@media screen and (max-width: 1023px) {
  .social-icons {
    justify-content: center;
  }
}

.social-icons a {
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  margin-left: .5rem;
  opacity: .5;
}

.social-icons a:hover {
  opacity: 1;
}

.talk {
  display: flex;
  position: relative;
}

.talk--speaker-profile {
  display: block;
}

.talk--speaker-profile .talk__speaker {
  width: 100%;
}

@media screen and (max-width: 1023px) {
  .talk {
    flex-wrap: wrap;
  }
}

.talk__speaker {
  flex: none;
  width: 30%;
  margin-right: 1rem;
}

.talk__info {
  width: calc(70% - 1rem);
}

.talk p {
  margin-bottom: 0;
}

.talk-break {
  background: linear-gradient(rgba(0, 123, 223, 0.9), rgba(0, 123, 223, 0.9)), url(../img/talk-break-presentacion.jpg) center/cover;
  padding: 1rem;
  border-radius: .25rem;
  text-align: right;
  color: #fff;
  font-weight: bold;
  font-family: "Open Sans", "sans-serif";
  position: relative;
}

@media screen and (min-width: 1024px) {
  .talk-break {
    padding: 2rem;
    font-size: var(--h3-font-size);
  }
}

.talk-break.second {
  background: linear-gradient(rgba(0, 203, 255, 0.9), rgba(0, 203, 255, 0.9)), url(../img/talk-break-coffe-break.jpg) center/cover;
}

.talk-break.third {
  background: linear-gradient(rgba(255, 60, 50, 0.9), rgba(255, 60, 50, 0.9)), url(../img/talk-break-premiacion.jpg) center/cover;
}

.talk__hour {
  font-size: var(--smaller-font-size);
  color: #FF3C32;
  font-family: Lato, "sans-serif";
}

@media screen and (max-width: 1023px) {
  .talk__hour {
    width: 100%;
    text-align: center;
    margin-bottom: .5rem;
  }
}

@media screen and (min-width: 1024px) {
  .talk__hour {
    position: absolute;
    font-weight: 700;
    top: 50%;
    transform: translateY(-50%);
    left: -8rem;
    width: 4rem;
    text-align: right;
  }
  .talk__hour span {
    font-size: 0;
    display: block;
  }
  .talk__hour::after {
    content: "";
    display: block;
    position: absolute;
    width: .6rem;
    height: .6rem;
    border: 1px solid;
    background: #E7F9FF;
    border-radius: 50%;
    right: -2.3rem;
    top: 50%;
    transform: translateY(-50%);
  }
}

@media screen and (min-width: 1024px) {
  .talk::before, .talk-break::before {
    content: '';
    width: 1px;
    background: #FF3C32;
    height: calc(100% + 5rem);
    position: absolute;
    left: -2rem;
    top: 50%;
  }
  .talk:last-child::before, .talk-break:last-child::before {
    display: none;
  }
}

.talk--speaker-profile::before, .talk-break--speaker-profile::before {
  display: none;
}

@media screen and (max-width: 1023px) {
  .talk-break {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .talk-break .talk__hour {
    color: #fff;
    width: 30%;
    margin-right: 1rem;
    margin-bottom: 0;
    text-align: left;
  }
}

.faq__item h3 {
  font-size: 1rem;
  color: #007BDF;
}

.faq__item p {
  font-size: var(--small-font-size);
}

.diagonal-section {
  position: relative;
}

.diagonal-section__content {
  position: relative;
  padding: 2rem 0 4rem;
}

.diagonal-section::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f6f6f6;
  transform: skewY(-1.5deg);
  border: 1px solid #DFE0E0;
}

.diagonal-section.left::before {
  transform: skewY(1.5deg);
}

